import React, { useState,useRef }  from 'react';
import { Menubar } from 'primereact/menubar';
import { Menu } from 'primereact/menu';
import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import axios from 'axios'
import { useLocalStorage } from '../requests/greenHooks.js'
import  {api_address }  from '../proxy/proxy.js'

//Menu com dropdown para pedidos,clientes e etc
const MenuPerfil = () => {
    const menu = useRef(null);
    const [username, ] = useLocalStorage("username",null)
    const [isRep, ] = useLocalStorage("isRep",null)
    
    let items_perfil = [
            {
                label: 'Conta',
                icon: 'pi pi-user',
                url: '/conta'
            },
            {
                label: 'Pedidos',
                icon: 'pi pi-upload',
                url: '/pedidos'
            },

        ]
        if (isRep){
            items_perfil.push({
                label: 'Promocoes',
                icon: 'pi pi-external-link',
                url: '/promocoes'
            })
            items_perfil.push({
                label: 'Clientes',
                icon: 'pi pi-external-link',
                url: '/clientes'
            })

        }    
    const items = [
        {   
            label:username,
            items: items_perfil
        }
    ]



    return (
        <>
            <Menu model={items} popup ref={menu} id="popup_menu" />
            <Button icon="pi pi-user" onClick={(event) => menu.current.toggle(event)} aria-controls="popup_menu" aria-haspopup />
        </>
    );
}


